function id(text) {
  return document.getElementById(text);
}

if (id("downloadButton"))
  id("downloadButton").addEventListener("click", download, false);
if (id("renderButton"))
  id("renderButton").addEventListener("click", render, false);

function render() {
  pdfMake.createPdf(docDef).getDataUrl(function(dataURL) {
    renderPDF(dataURL, id("canvas"));
  });
}
function download() {
  var pdf = createPdf(docDef);
  pdf.download("PPRA.pdf");
}

render();

// * this is not important for PDFMake, it's here just to render the result *
// It's a Mozilla lib called PDFjs that handles pdf rendering
function renderPDF(url, canvasContainer, options) {
  options = options || { scale: 1.4 };

  function renderPage(page) {
    var viewport = page.getViewport(options.scale);
    var wrapper = document.createElement("div");
    wrapper.className = "canvas-wrapper";
    var canvas = document.createElement("canvas");
    var ctx = canvas.getContext("2d");
    var renderContext = {
      canvasContext: ctx,
      viewport: viewport
    };

    canvas.height = viewport.height;
    canvas.width = viewport.width;
    wrapper.appendChild(canvas);
    canvasContainer.appendChild(wrapper);

    page.render(renderContext);
  }

  function renderPages(pdfDoc) {
    for (var num = 1; num <= pdfDoc.numPages; num++)
      pdfDoc.getPage(num).then(renderPage);
  }

  PDFJS.disableWorker = true;
  PDFJS.getDocument(url).then(renderPages);
}
